.header {
  background-color: #000;
  &__container {
    position: relative;
    height: 230px;
    padding-top: 40px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

    @include breakpoint(tablet) {
      height: 216px;
    }

    &_home {
  // background-image: $bg-gradient, url('src/images/home-mob-1x.jpg');
  background-image: linear-gradient(#00000056, #00000056), url('../images/home-mob-1x.jpg');
// background-image: $bg-gradient, url('../images/home-mob-1x.jpg'); 
      @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: linear-gradient(#00000056, #00000056), url('../images/home-mob-2x.jpg');
      }

      @include breakpoint(tablet) {
        background-image: linear-gradient(#00000056, #00000056), url('../images/home-tab-1x.jpg');

        @media (min-device-pixel-ratio: 2),
          (min-resolution: 192dpi),
          (min-resolution: 2dppx) {
          background-image: linear-gradient(#00000056, #00000056), url('../images/home-tab-2x.jpg');
        }
      }

      @include breakpoint(desktop) {
        background-image: linear-gradient(#00000056, #00000056), url('../images/home-desk-1x.jpg');

        @media (min-device-pixel-ratio: 2),
          (min-resolution: 192dpi),
          (min-resolution: 2dppx) {
          background-image: linear-gradient(#00000056, #00000056), url('../images/home-desk-2x.jpg');
        }
      }
    }

